.footer {
  width: 60%;
  display: flex;
  flex-wrap: wrap;
  background-color: #1e1e1e;
  color: white;
  justify-content: space-around;
  align-items: center;
  min-height: 45px;
  margin-top: 24px;
  margin-bottom: 24px;
  border-radius: 20px;
  -webkit-border-radius: 8px;
  -moz-border-radius: 8px;
  margin-left: auto;
  margin-right: auto;
  /* the above two css is equivalent to margin : auto */
}
.footer__left {
  color: var(--main-color);
  /* text-decoration: none !important; */
  cursor: pointer;
}

@media only screen and (max-width: 991px) {
  .footer {
    flex-direction: column;
    padding-top: 4px;
    padding-bottom: 7px;
  }
}
